import React, { Component } from 'react';
import { Carousel,Card ,Button} from 'antd';

import LbMove from './Lunbo'
import "antd/dist/antd.css";
import './home.css'
import axios from 'axios'
import {NavLink} from "react-router-dom";



const { Meta } = Card;
class Home extends Component {

    constructor(props){
        super(props)
        this.state = {
            data : [],
            list:[]
        }
    }

    componentDidMount(){
        axios({
            url:'http://localhost:8000/api/demo'
        }).then(res=>{
            
            this.setState({data:res.data.data.lists})
        })
        axios({
            url:'http://localhost:8000/homelist'
        }).then(res=>{
            console.log(res.data.data)
            this.setState({list:res.data.data})
        })
        
    }

    tap(id){
        console.log(id)

    }
    
    render() {

        return ( 
            <div>
                <Carousel autoplay effect="fade">
                    {
                        this.state.data.map((item,i)=>(
                            <NavLink key={i} to={{ pathname: '/detail' , state : { id: i}}}>
                                <div className="banner">
                                <img src={item.imgSrc} alt="1" />
                                </div>
                            </NavLink>
                        ))
                    } 
                                            
                 </Carousel>
               
                 
                 <div className='title'>
                 <h1><NavLink to="http://www.ttpcn.com/lines/all/">『不忘初心,追光逐影』</NavLink></h1>
                 <p>生活不止眼前的苟且还有诗和远方</p>
                 </div>
                                

               

                
                <Card
                    className="card"
                    hoverable='true'
                    cover={<img alt="example" src="http://www.ttpcn.com/uploads/2018/0925/6cdd477bfe1d4d9bdcb653160f40a5cd.jpg" />}
                >
                    <Meta title="【绝美山峰】" description="意大利多洛米蒂斯洛文尼亚山区深度摄影之旅" />
                </Card>
                <Card
                    className="card"
                    hoverable
                    cover={<img alt="example" src="http://www.ttpcn.com/uploads/2019/0115/37db094106c351b528908d779ec27db5.jpg" />}
                >
                    <Meta title="【冰火环境】" description="夏季冰岛内陆高地及南岸深度摄影之旅" />
                </Card>
                <div className="scard">
                    {
                    this.state.list.map((item,i)=>(
                        <Card
                        className="smallcard"
                        key={i}
                        hoverable
                        cover={<img alt="example" src={item.imgSrc} />}
                        >
                        <Meta title={item.title} description="夏季冰岛内陆高地及南岸深度摄影之旅"  />
                        </Card>
                    ))
                }
                </div>
                


                <div className='btn'>
                    
                    <Button size='large'>浏览更多</Button>
                
                </div>

                <div className='title'>
                 <h1><NavLink to="http://www.ttpcn.com/lines/all/">『走过远方，精彩典藏』</NavLink></h1>
                 <p>亲临秘境，与您影藏世界之美</p>
                 </div>
                 <div class="Box">
    	<div class="content">
    		
    		
    		<div class="Box_con clearfix">
    			
    			<div class="conbox" id="BoxUl">
	    			<ul>
	    				<li class="cur"><img src="http://www.ttpcn.com/uploads/2019/0115/37db094106c351b528908d779ec27db5.jpg" alt=""/></li>
	    				
	    				<li class="cur"><img src="http://www.ttpcn.com/uploads/2018/0903/5de10181c44e479be76e6e78b0144488.jpg" alt=""/></li>
	    				
	    				<li class="cur"><img src="http://www.ttpcn.com/uploads/2018/0903/878efd3b8fec5fd8381fae674df4c36a.jpg" alt=""/></li>
	    			
	    				<li class="cur"><img src="http://www.ttpcn.com/uploads/2018/0903/3ad9395a24a919efe5b8f387a26e4233.jpg" alt=""/></li>
	    				
	    				<li class="cur"><img src="http://www.ttpcn.com/uploads/2018/0903/9506177a9174be57c0f31edd94167020.jpg" alt=""/></li>
    				</ul>
    			</div>
    			
    			<p class="BoxSwitch" id="BoxSwitch">
    				<span class="cur"></span>
    				<span></span>
    				<span></span>
    				<span></span>
    			</p>
    			
    		</div>
            <div className='btn'>
                    
                    <Button size='large'>浏览更多</Button>
                
                </div>
    		
    	</div>
    </div>
    
    {
        LbMove('BoxUl','btnl','btnr','BoxSwitch',true,'left',true,305,1000,5000,4)
    }
    
       <footer>

        <ul>
            <li>关于我们</li>
            <li>联系我们</li>
            <li>支付方式</li>
        </ul>
        <p className='shendu'>深度行摄影旅游网版权所有 备案号 蜀ICP备13021114号-1</p>
        <p>Copyright ® Chengdu Top Travel Photography Co.,Ltd. All right reserved.</p>
        </footer>
            </div> 
        );
    }
}




export default Home;


