import React, { Component } from 'react'
import './person.css'
import { Timeline } from 'antd';
import { Input } from 'antd';
import { DatePicker, Button } from 'antd';
const { MonthPicker, RangePicker, WeekPicker } = DatePicker;
export default class Person extends Component {
  
  
 onChange=(date, dateString)=> {
  console.log(date, dateString);
}
  render() {
    return (
      <div className='box'>
        <div >
          <Timeline className='timeline'>
            <Timeline.Item className='item'>10秒提交需求</Timeline.Item>
            <Timeline.Item className='item'>2小时与您沟通</Timeline.Item>
            <Timeline.Item className='item'>24小时获得专属方案</Timeline.Item>
          </Timeline>
        </div>
        <div> 
          
          <div className='change'>
            <h2>开始定制</h2>
        <DatePicker onChange={this.onChange} className='date' placeholder="请选择出发时间"/>
        <Input className='input' placeholder="请输入目的地" />
        <Input className='input' placeholder="请填写人数" />
        <Input className='input' placeholder="请填写姓名" />
        <Input className='input' placeholder="联系手机" />
        <Input className='input' placeholder="联系qq" />
        <Button type="primary" size='large'>提交定制需求</Button>
        <div class="pic1"></div>
        <div class="pic2"></div>
          </div>


        </div>
      </div>
    )
  }
}
