import React, { Component } from 'react'
import axios from 'axios'
import './detail.css'
import { Tabs ,Button} from 'antd';
import {NavLink} from "react-router-dom";
const TabPane = Tabs.TabPane;
export default class Detail extends Component {
    constructor(props){
        super(props)
        this.state={
            detail:{}
        }
    }
    componentDidMount(){
        
        const id = this.props.location.state.id

        axios({
            url:'http://localhost:8000/api/detail',
            params:{
                id : id
            }
        }).then(res=>{
            console.log(res.data.data)
            this.setState({detail:res.data.data})
        })
    }
    callback=(key)=> {
        console.log(key);
        }

  render() {

    return (
      <div>
          
              
                  
                 <div className='box'>
                     <h1>{this.state.detail.title}</h1>
                    <img src={this.state.detail.imgSrc} alt=""/>
                    <div className='content'>
                         <p>{this.state.detail.desc}</p>
                        <p>日程：&nbsp;{this.state.detail.date}</p>
                        <p>天数：&nbsp;{this.state.detail.time}</p>
                        <p>联系电话：&nbsp;{this.state.detail.phone}</p>
                        <p>优惠价格：&nbsp;<span>￥{this.state.detail.price}</span></p> 
                        <Button type="primary" size='large'><NavLink to={{ pathname: '/pay' , state : { price: this.state.detail.price,title:this.state.detail.title}}}>立即报名</NavLink></Button>
                    </div>

                 </div>
                
                 
                 <Tabs onChange={this.callback} type="card" className='tabs'>
                    <TabPane tab="行程亮点" key="1" className='tab'>
                    <p>独家行程：我们从专业角度来安排行程，选择最佳季节出行，注重每天早晚黄金时间的合理安排，行程全面覆盖意大利北部多洛米蒂山区和斯洛文尼亚山区当季的精华景观。<br/>
                        特别安排：特别安排在多洛米蒂和斯洛文尼亚的夏季最美的时间段出行，同时兼顾了夏季银河的最佳拍摄日期，我们分别多洛米蒂山区安排了6个早晚和在斯洛文尼亚安排了3天的拍摄时间，有充分的时间来拍摄这两个地区的美丽风光。<br/>
                        新增斯洛文尼亚增加一处国家地理编辑推荐的山顶教堂拍摄地和另外一处山顶教堂，绝对深入的摄影行程，只为你拍出极为少见的风光大片。贴心服务：李健老师亲自领航，他将结合本地区多次的拍摄经验，带领我们深入秘境，进入常规团不会深入的地方进行创作，将让你满载而归。<br/>
                        精品小团：6人成行，全团最多12人。严格控制人数。<br/>
                        资深指导：移动式课堂，资深摄影导师、深度行创始人李健全程现场教学，前后期结合，于实战中迅速提高摄影技能，及时专场看片点评，适时传授摄影要领和后期要诀，总结提高。<br/>
                        全时创作：全时段均为摄影创作时间，行程不会受到司机工作时间的影响，保证早晚都有拍摄内容，无任何自费安排！<br/>
                        品质保障：全程离拍摄点最近的4-3星级酒店，其中一晚可能背包客栈。中西餐结合，西餐均为两道式正餐。<br/>
                        【摄影指导】<br/>
                        李健，一直行走在世界边缘的资深环球旅行摄影师，摄影书籍《上帝之眼2》合作摄影师，作品《星耀雪山》入选2015年美国《国家地理》全球摄影大赛中国赛区自然类50佳，多幅作品刊登于《厦门航空》、《中国国家旅游》、《影像生活》等杂志，其拍摄的巴塔哥尼亚和伊瓜苏瀑布作品被阿根廷住中国大使馆收藏。喜欢探险摄影的他，30余次进藏，10多次前往新疆，8次前往冰岛，7次前往缅甸，5次前往多洛米蒂和斯洛文尼亚山区，4次前往格陵兰岛，3次前往南美巴塔哥尼亚拍摄，足迹遍布中国西部地区和世界五大洲40多个国家。</p>
                    </TabPane>
                    <TabPane tab="行程安排" key="2">
                    <p>
                    Day 1（6.26日）：出发日，从世界各地飞往意大利威尼斯马可波罗机场<br/>
                        Day 2（6.27日）：集合日，意大利威尼斯马可波罗机场→布莱德湖<br/>
                        当地时间下午一点前达到意大利威尼斯马可波罗机场。导游在机场举牌接机。接机后驱车前往斯洛文尼亚布莱德湖。布莱德湖：阿尔卑斯山脉南麓最有名的旅游胜地之一。远处是多座云遮雾置超过两千米以上的阿尔卑斯雪峰，湖中耸立着古老的圣玛利亚教堂，以及湖边陡峭的岩壁上盘踞着十二世纪的布莱德城堡，自由自在的天鹅和野鸭在静如止水的湖面上游弋，放佛童话一般。布莱德湖无论日出日落、春夏秋冬、阴晴雾雪，都能捕捉到绝美的瞬间。
                        住宿：布莱德湖四星级酒店（含午晚餐）Day3-D4（6.28-6.29日）：两天山区小镇拍独特的山顶教堂
                        早起登上制高点拍摄布莱德湖日出，布莱德湖因其特殊的地理位置和气候的影响，日出前后会出现梦幻般的粉色，一段时间以后雾气慢慢消散开，随着太阳光的渐变，便又会呈现出耀眼夺目的金色。登上制高点，凭栏远眺，布莱德湖的激滟风光尽收眼底，远处的阿尔卑斯山层峦叠峰一直沿向天际。
                        午餐之后前往山区小镇。接下来的两天我们将拍摄斯洛文尼亚独具特色的山顶教堂日落。初夏的山区，早晨多有晨雾，层峦叠峰的远山，若影若现的教堂让人流连忘返，仿佛置身于仙境！为保护行程的原创性，我们对行程和地点不做过多的描述。拍摄内容请参看图片。
                        住宿：斯洛文尼亚山区小镇三星级酒店
                        Day5（6.30日）：布莱德湖→多罗米蒂山山区<br/>
                        早上再次前往山区小镇进行拍摄独具特色的山顶教堂日出及晨光中的山区景色，然后驱车前往多洛米蒂山区。经过3小时的车程，我们将到达我们在多洛米蒂山区的第一个A风
                        景绝美，入住后直接去拍摄日落。如果天气好，拍完日落后我们将留下来继续等待拍摄银河星空。
                    </p>
                    </TabPane>
                    <TabPane tab="团期和费用" key="3">
                        <p>
                        【行程时间】2019.6.26-2019.7.7<br/>
                        【行程价格】<br/>
                        26800元/人，单房6000元
                        本价格不包含发票和国际机票，开票需另交6%的税金。
                        【费用说明】<br/>
                        费用包含：
                        【住宿】此条线路为摄影量身订作，非常规旅游线路，我们尽可能安排距离拍摄点距离较近的酒店。大城市都是安排4星级酒店，小镇和山区是3-4星级酒店。酒店均按2人一间房核算费用，如一人住一间，需补房差差额人民币9000元/人。<br/>【导游】意大利当地中文司机+深度行资深摄影指导
                        【餐饮】行程中全程餐食，飞机上以飞机餐为准。全程以西餐和当地特色餐为主，在有条件的地方搭配中餐调节。<br/>
                        【交通】旅游小巴；<br/>
                        【门票】全程所列景点的所有门票。<br/>
                        【保险】我社已为每位客人代购“保额为100万境外旅游险”，在行程期间，出现非旅行社责任造成的意外损失，由保险公司进行鉴定与赔付，赔付办法按照相关保险条例，
                        </p>
                    </TabPane>
                    <TabPane tab="温馨提示" key="4">
                    <p>
                    1、一般情况下，70岁以上的影友提供1个月以内的健康证明方可参加。<br/>
                    2、本行程个别拍摄点需要徒步一定的距离才能到达，为确保行程的顺利进行，建议你出发之前进行适当的锻炼。<br/>
                    3、是否给予签证、是否准予出、入境，为有关机关的行政权利。如因游客自身原因或因提供材料存在问题不能及时办理签证而影响行程的，以及被有关机关拒发签证或不准出入境的，相关责任和费用由游客自行承担。<br/>
                    4、因汇率的大幅振荡而导致的成本增加，我公司将根据实际情况保留调价的权利。<br/>
                    5、听从向导及摄影指导师的安排，不要单独行动，不要翻越栏杆及到悬崖边拍照，时刻注意安全。<br/>
                    6、为了保障以上摄影创作的圆满完成以及团友人身安全，我们有权拒绝行程以外的其它要求。
                    </p>
                    </TabPane>
                    <TabPane tab="行程样片" key="5">

                    </TabPane>
                </Tabs>





  

          
      </div>
    )
  }
}
