import React, { Component } from 'react'
import { Card, Col, Row,InputNumber ,Button,Input,message} from 'antd';

import axios from 'axios'
import './pay.css'

export default class pay extends Component {
  constructor(props){
    super(props)
    this.state={
      totalprice:'',
      price:parseInt(this.props.location.state.price),
      persons:'',
      type:this.props.location.state.title
    }
  }
  success = () => {
    message.success('报名成功');
  };
  onChange=(value) =>{
    console.log(value);
    this.setState({totalprice:value*this.state.price})
    this.setState({persons:value})
  }

  submit=()=>{
    var that = this
    console.log(that.refs.type.children)
    axios({
      method:"POST",
      url:'http://localhost:8000/order',
      data:{
        name:that.refs.name.state.value,
        phone:that.refs.phone.state.value,
        email:that.refs.email.state.value,
        msg:that.refs.msg.state.value,
        price:that.state.price,
        persons:that.state.persons,
        type:that.state.type
      }
    }).then(res=>{
      
      if(res.status===200){
        this.success()
      }
    })

  };
   


  render(){
    return (
      <div>
        <div className='paybox'>

          <div className='buzhou'>
            <img src="http://www.ttpcn.com/templets/smore/images/liucheng.gif" alt=""/>
            
            
          </div>

          <div >
              <Card title="产品编号：A436" extra={<a href="#">More</a>} style={{ width: 880 }}>
                <p ref="type">产品名称：【绝色山峰 】意大利多洛米蒂斯洛文尼亚山区深度摄影之旅(2019.6.26)</p>
                <p>产品类型：2019.6.26</p>
                <p>出发日期：2019-06-26</p>
              </Card>
              <div style={{ background: '#fff', padding:"30px 30px",marginTop:"20px" }}>
                <Row gutter={20}>
                  <Col span={4}>
                    <Card title="类型" bordered={false}>
                      成人
                    </Card>
                  </Col>
                  <Col span={4}>
                    <Card title="日期" bordered={false}>
                      2019-06-06
                    </Card>
                  </Col>
                  <Col span={4}>
                    <Card title="单价" bordered={false}>
                      {this.state.price}
                    </Card>
                  </Col>
                  <Col span={4}>
                    <Card title="购买数量" bordered={false}>
                    <InputNumber min={1} max={10} defaultValue={3} onChange={this.onChange} />
                    </Card>
                  </Col>
                  <Col span={4}>
                    <Card title="金额" bordered={false}>
                      {this.state.totalprice}
                    </Card>
                  </Col>
                </Row>
              </div>,
              <Card title="预定信息 &nbsp;手机号码快速下单，无需注册登录！" extra={<a href="#">More</a>} style={{ width: 880 }}>
              <p>预定联系人：<Input placeholder="姓名" ref="name" type='text'/></p>
              <p>联系手机：<Input placeholder="手机号" ref="phone" type='number'/></p>
              <p>邮箱：<Input placeholder="邮箱地址" ref="email" type='text'/></p>
              <p>订单留言：<Input placeholder="有什么需要想说的吗" ref='msg' type='text'/></p>
              </Card>
              
              <div>
              <Button type="primary" block onClick={this.submit} style={{ marginTop:"20px" }}>
                提交信息
              </Button>
              </div>
            </div>


        </div>
      </div>
      )
    }
  
}
