import React, { Component } from 'react';
import { Card, Button } from 'antd';

import './probe.css'
import "antd/dist/antd.css";
import './home.css'
import axios from 'axios'
import { HashRouter as Router} from "react-router-dom";



const { Meta } = Card;
class Show extends Component {

  constructor(props) {
    super(props)
    this.state = {

      list: []
    }
  }

  componentDidMount() {

    axios({
      url: 'http://localhost:3000/api/homelists'
    }).then(res => {
      console.log(res.data.data.lists)
      this.setState({ list: res.data.data.lists })
    })

  }

  tap(id) {
    console.log(id)

  }

  render() {

    return (
      <Router>
        <div>
          <div className="scard">
            {
              this.state.list.map((item, i) => (
                <Card
                  className="smallcard"
                  key={i}
                  hoverable
                  cover={<img alt="example" src={item.imgSrc} />}
                >
                  <Meta title={item.title} description="夏季冰岛内陆高地及南岸深度摄影之旅" />
                  <p>出行时间:{item.date} &nbsp; 行程天数:{item.time}天</p>
                  <p>优惠价：<span className='price'>{item.price}</span></p>
                </Card>
              ))
            }
          </div>



          <div className='btn'>

            <Button size='large'>浏览更多</Button>

          </div>








          <footer>

            <ul>
              <li>关于我们</li>
              <li>联系我们</li>
              <li>支付方式</li>
            </ul>
            <p className='shendu'>深度行摄影旅游网版权所有 备案号 蜀ICP备13021114号-1</p>
            <p>Copyright ® Chengdu Top Travel Photography Co.,Ltd. All right reserved.</p>
          </footer>
        </div>
      </Router>
    );
  }
}




export default Show;