import React, { Component } from 'react';
import { Menu } from 'antd';
import "antd/dist/antd.css";
import "./App.css"

import { HashRouter as Router, Route,Link,NavLink} from "react-router-dom";
import routes from './router/routes';

class App extends Component {
  state = {
    current: 'mail',
  };

  handleClick = e => {
    
    this.setState({
      current: e.key,
    });
  };

  render() {
    return (
      <Router>
      <div>
      <Menu theme="dark"  onClick={this.handleClick}  mode="horizontal" className="navbar">
        <Menu.Item key="logo" className="navies">
        <NavLink to='/home'> <img className='img' src="http://www.ttpcn.com/uploads/main/allimg/20151216/20151216144124.png" alt=""/></NavLink>

        </Menu.Item>
        <Link className="navies" to='/probe'>极地探索</Link>
        <NavLink className="navies" to='/photo'>境外摄影</NavLink>
        <NavLink className="navies" to='/trip'>国内行程</NavLink>
        <NavLink className="navies" to='/show'>作品展示</NavLink>
       <NavLink className="navies" to='/person'>私人定制</NavLink>
       
      </Menu>
      
            {
              routes.map((route,key)=>{
                if(route.exact){
                  return <Route key={key} exact path={route.path} component={route.component}/>
                }else{
                  return <Route  key={key}  path={route.path} component={route.component}/>
                }
              })
            }
      
      </div>
      </Router>
    );
  }
}

export default App;
